import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import layout from '@/components/layout/layout'
import jichulayout from '@/components/layout/defaultview'
import jiange from '@/components/layout/gutter'
import hunhe from '@/components/layout/mix'
import pianyi from '@/components/layout/pianyi'
import duiqi from '@/components/layout/duiqi'
import xiangyingshi from '@/components/layout/xiangyingshi'

import color from '@/components/color/color'
import zhuse from '@/components/color/zhuse'
import fuzhuse from '@/components/color/fuzhuse'
import zhongxingse from '@/components/color/zhongxingse'

import font from '@/components/font/font'

import button from '@/components/button/button'
import jichu from '@/components/button/jichu'
import jinyong from '@/components/button/jinyong'
import yanse from '@/components/button/yanse'
import tubiao from '@/components/button/tubiao'
import anniuzu from '@/components/button/anniuzu'
import jiazai from '@/components/button/jiazai'
import size from '@/components/button/size'
Vue.use(Router)
export default new Router({
  routes: [
    {path:'/',component:layout},
    {path:'/layout',component:layout,children:[
      {path:'/' ,component:jichulayout},
      {path:'jichulayout',component:jichulayout},
      {path:'jiange' ,component:jiange},
      {path:'hunhe',component:hunhe},
      {path:'pianyi',component:pianyi},
      {path:'duiqi',component:duiqi},
      {path:'xiangyingshi',component:xiangyingshi},
    ]},
    {path:'/color',component:color,children:[
      {path:'/',component:zhuse},
      {path:'zhuse',component:zhuse},
      {path:'fuzhuse',component:fuzhuse},
      {path:'zhongxingse',component:zhongxingse}
    ]},
    {path:'/font',component:font},
    {path:'/button',component:button,children:[
      {path:'',component:jichu},
      {path:'jichu',component:jichu},
      {path:'jinyong',component:jinyong},
      {path:'yanse',component:yanse},
      {path:'tubiao',component:tubiao},
      {path:'anniuzu',component:anniuzu},
      {path:'jiazai',component:jiazai},
      {path:'size',component:size}
    ]}
  ]
})



