<template>
  <div>
    <div class="nav">
      <router-link to="/color/zhuse">主色</router-link>
      <router-link to="/color/fuzhuse">辅助色</router-link>
      <router-link to="/color/zhongxingse">中性色</router-link>
    </div>
    <router-view/>
