</template>

<script>
import Vue from "vue";
import Element from "element-ui";
import "element-ui/lib/theme-default/index.css";
Vue.use(Element);
export default {};
</script>

<style>
.nav {
  width: 100%;
  height: 30px;
  background: #20a0ff;
  display: flex;
  justify-content: flex-start;
}
.nav a {
  display: block;
  width: 5em;
  height: 30px;
  font-size: 14px;
  color: white;
  text-decoration: none;
  margin-right: 20px;
}
</style>



